import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import validation from './validationBusiness';
import { useState, useContext, useEffect } from 'react';
import { padding } from '@mui/system';
import CloseIcon from '@mui/icons-material/Close';

import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import { GlobalContext } from '../context/GlobalState';

import { createSubscriber } from '../context/actions/subscriberAction/addSubscriber';
import { Grid, TextField } from '@mui/material';
import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';

import Fab from '@mui/material/Fab';
import MessageIcon from '@mui/icons-material/Message';

const init = {
  subscriber_name: '',
  subscriber_email: '',
  subscriber_phone: '',
  subscriber_campany_name: '',
  subscriber_message: ''
};

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const validateEmail = (email) =>
  String(email)
    .toLowerCase()
    .match(
      /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    );


// const validateGmail = (email) =>
// String(email)
//   .match(
//     'gmail'
//   );



const style = {
  position: 'absolute',
  top: '15%',
  left: '33%',
  border: '2px solid red',
};

export default function Find_Taskers_Common(props) {
  console.log('aaa', props)
  const { subscriberState, subscriberDispatch } = useContext(GlobalContext);

  const [state, setState] = useState(init);
  const [store, setStore] = useState([]);
  const [number, setNumber] = useState([])
  const [errors, setErrors] = useState({});
  const [open, setOpen] = useState(false);

  const cars = ['AUDI R8 V10', 'BAARAT EXPERIENCE', 'BENTLEY GTC', 'BENTLEY BENTYAGA', 'BMW M4 COMPETITION', 'CORVETTE', 'EXCALIBUR (VINTAGE)', ' FERRARI 488', 'LAMBORGHINI HURACAN', '1956 PORSCHE SPEEDSTER', 'ROLLS ROYCE DAWN', 'MCLAREN 720S']

  const locations = ['New York', 'New Jersey', 'Philadelphia', 'Washington D.C', 'Boston', 'Los Angeles', 'Cancun', 'Puerto Rico']
  const handleClose = () => {
    setErrors(false)
    setState(init);
    setOpen(false)
  }

  // if(state.subscriber_email.match("gmail")){
  //   errors.subscriber_email = 'Please enter official email';
  // }

  const handleChangeNumberLeadInput = (event) => {
    console.log('event', event.target.value)
    const onlyNums = event.target.value.replace(/[^0-9]/g, '');
    console.log('onlyNums', onlyNums)
    if (onlyNums.length <= 10) {
      setNumber(onlyNums)
      setState({
        ...state,
        [event.target.name]: onlyNums,
      });
    }
  }



  const handleChange = (e) => {
    const { name, value } = e.target;
    setState({ ...state, [name]: value });
  };


  const handleClick = e => {
    e.preventDefault()
    setErrors(validation(state));

    if (
      state.subscriber_name !== '' &&
      state.subscriber_email !== '' &&
      !state.subscriber_email.match("gmail") &&
      validateEmail(state.subscriber_email) &&
      // validateGmail(!state.subscriber_email) &&   
      state.subscriber_phone !== '' &&
      state.subscriber_phone.length === 10 &&
      state.subscriber_campany_name !== '' &&
      state.subscriber_message !== ''
    ) {
      const postData = {
        subscriber_name: state.subscriber_name,
        subscriber_email: state.subscriber_email,
        subscriber_phone: state.subscriber_phone,
        subscriber_campany_name: state.subscriber_campany_name,
        subscriber_message: state.subscriber_message,
        subscriber_type: props.subscriber_type,
        visitor_type: 'hire_tasker'
      };
      createSubscriber(postData)(subscriberDispatch)
      setState(init);
      setOpen(true);
      console.log('Create Subscriber Detail', postData)
    }


  }



  useEffect(() => {
    console.log('subscriberState', subscriberState)
    let status = subscriberState.createSubscriber && subscriberState.createSubscriber.data && subscriberState.createSubscriber.status
    if (status == true) {
      console.log("store", store);
      subscriberState.createSubscriber = {
        loading: false,
        error: null,
        data: null,
      }
      window.location.replace('b2b-services/thank-you')
    }
  }, [subscriberState])


  return (
    <div>
      <Snackbar open={open} onClose={handleClose} autoHideDuration={2000} anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}>
        <Alert severity="success" sx={{ width: '100%' }}>
          Form Successfully Submitted!
        </Alert>
      </Snackbar>
      <h4 id='enquiry-form1' style={{ lineHeight: "1.3em", color: '#fff', textAlign: 'center' }} class="pb-20 font-montserrat fs-20 fw-600">CONNECT WITH OUR TEAM</h4>

      <div class="consultation-form" style={{ background: '#242424' }}>

        <Grid container xs={12}>
          <Grid padding={1} xs={6} sm={6} md={3} lg={3}>
            <label style={{ color: '#fff' }}>NAME*</label>
            <TextField fullWidth label="First Name*"
              inputProps={{
                style: {
                  height: "20px",
                  background: '#fff'
                },
              }} variant="outlined" type="text" name="subscriber_name" value={state.subscriber_name} onChange={handleChange} />
            {errors.subscriber_name && <p style={{ color: 'red' }}>{errors.subscriber_name}</p>}
          </Grid>

          <Grid padding={1} xs={6} sm={6} md={3} lg={3}>
            <label style={{ color: '#fff' }}> &nbsp;</label>

            <TextField fullWidth label="Last Name*"
              inputProps={{
                style: {
                  height: "20px",
                  background: '#fff'
                },
              }} variant="outlined" type="text" name="subscriber_name" value={state.subscriber_name} onChange={handleChange} />
            {errors.subscriber_name && <p style={{ color: 'red' }}>{errors.subscriber_name}</p>}
          </Grid>

          <Grid padding={1} xs={12} sm={12} md={6} lg={6}>
            <label style={{ color: '#fff' }}>EMAIL</label>

            <TextField fullWidth

              inputProps={{
                style: {
                  height: "20px",
                  background: '#fff'

                },
              }} variant="outlined" type="email" name="subscriber_email" value={state.subscriber_email} onChange={handleChange} />
            {errors.subscriber_email && <p style={{ color: 'red' }}>{errors.subscriber_email}</p>}
          </Grid>

          <Grid padding={1} xs={12} sm={12} md={6} lg={6}>
            <label style={{ color: '#fff' }}>PHONE NUMBER*</label>

            <TextField fullWidth
              inputProps={{
                style: {
                  height: "20px",
                  background: '#fff'

                },
              }} variant="outlined" type="text" name="subscriber_phone" value={state.subscriber_phone} onChange={handleChangeNumberLeadInput} />
            {errors.subscriber_phone && <p style={{ color: 'red' }}>{errors.subscriber_phone}</p>}
          </Grid>


          <Grid padding={1} xs={12} sm={12} md={6} lg={6}>
            <label style={{ color: '#fff' }}>LOCATION PREFERANCE*</label>

            <FormControl sx={{ minWidth: '100%' }} >

              {/* <InputLabel id="demo-select-small-label">Location Preferance</InputLabel> */}
              <Select
                labelId="demo-select-small-l  abel"
                id="demo-select-small"
                value={state.subscriber_campany_name}
                style={{
                  background: '#fff'
                }}
                onChange={handleChange}
              >
                {locations.map((name) => (
                  <MenuItem key={name} value={name} style={{ textTransform: 'uppercase' }}>
                    {name}
                  </MenuItem>
                ))}

              </Select>
            </FormControl>

            {errors.subscriber_campany_name && <p style={{ color: 'red' }}>{errors.subscriber_campany_name}</p>}
          </Grid>

          <Grid padding={1} xs={12} sm={12} md={6} lg={6}>
            <label style={{ color: '#fff' }}>VENUE*</label>

            <TextField fullWidth
              inputProps={{
                style: {
                  height: "20px",
                  background: '#fff'

                },
              }} variant="outlined" type="text" name="subscriber_campany_name" value={state.subscriber_campany_name} onChange={handleChange} />
            {errors.subscriber_campany_name && <p style={{ color: 'red' }}>{errors.subscriber_campany_name}</p>}
          </Grid>

          <Grid padding={1} xs={12} sm={12} md={6} lg={6} >
            <label style={{ color: '#fff' }}>SELECT CARS*</label>
            <FormControl sx={{ minWidth: '100%' }} >

              {/* <InputLabel id="demo-select-small-label">Select Cars</InputLabel> */}
              <Select
                labelId="demo-select-small-l  abel"
                id="demo-select-small"
                value={state.subscriber_campany_name}
                style={{
                  background: '#fff'
                }}
                onChange={handleChange}
              >
                {cars.map((name) => (
                  <MenuItem key={name} value={name} style={{ textTransform: 'uppercase' }}>
                    {name}
                  </MenuItem>
                ))}

              </Select>
            </FormControl>

            {errors.subscriber_campany_name && <p style={{ color: 'red' }}>{errors.subscriber_campany_name}</p>}
          </Grid>
        </Grid>

        <div class="input-field" style={{ padding: '8px', width: '200px' }}>
          <button style={{ fontWeight: "500", background: '#B88B2B' }} onClick={handleClick} type="submit" class=" template-btn">INQUIRE</button>
        </div>

      </div>
    </div>
  );
}
